<template>
	<view class="card">
		<view class="cardBox"
			v-for="(item,index) in items"
			:keys="index">
			<view class="post">
			<view class="content">
				<text class="content-text" selectable="true">{{item.content}}</text>
			</view>
			<!-- anonymity info -->
			<view class="head">
				<!--#@# head width:100% + padding > width:100%,超宽了， calc属性用起来-->
				<image src="../../static/image/pentagon.png" class="head-profile" mode="aspectFill"></image>
				<text class="head-name">{{item.userName}}</text>
				<text class="head-time">{{item.date}}</text>
			</view>
		</view>
		<!-- 加一个v-if判断是否有回复 -->
		<view v-if="item.reply" class="foot">
		</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				items: [{
					id: 0,
					content: '你想要成为rap star吗, rap star啊？我是朴宰范，u should know the name, u should know the name',
					userName: '示例昵称一共九个字',
					date: '3分钟前',
					reply: true
				},{
					id: 1,
					content: '你想要成为rap star吗, rap star啊？',
					userName: '示例昵称共八个字',
					date: '半小时前',
					reply: false
				},{
					id: 2,
					content: '我是朴宰范，u should know the name, u should know the name',
					userName: '示例昵称',
					date: '一天前',
					reply: true
				}]
			}
		}
	}
</script>

<style>
	.card {
		position: relative;
		flex-direction: column;
		align-items: center;
	}

	.cardBox {
		margin-top: 30px;
		margin-bottom: 20px;
	}
		
	.post {
		display: flex;
		flex-direction: column;
		width: 100%;
		background: #444444;
		border-radius: 15rpx 15rpx 0rpx 0rpx;
	}

	.content {
		display: flex;
		flex-direction: row;
		overflow: auto;
		padding-top: 23px;
		margin-left: 16px;
		margin-right: 16px;
	}

	.content-text {
		display: flex;
		max-width: 563px;
		font-size: 16px;
		font-family: Microsoft YaHei;
		color: #FFFFFF;
		opacity: 1;
		line-height: 16px;
	}

	.head {
		display: flex;
		width: calc(100% - 32px);
		flex-direction: row;
		padding-left: 16px;
		padding-top: 16px;
		padding-bottom: 14px;
		justify-content: flex-start;
	}

	.head-profile {
		width: 14px;
		height: 14px;
		margin-right: 5px;
	}

	.head-name {
		display: flex;
		font-size: 10px;
		font-family: Microsoft YaHei;
		font-weight: bold;
		line-height: 10px;
		color: #888888;
		margin-top: 2px;
		margin-right: 10px;
	}

	.head-time {
		font-size: 10px;
		color: #D5D5D5;
		max-width: 80px;
		display: inline-block;
		line-height: 10px;
		margin-top: 2px;
	}
	
	.foot {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: calc(100% - 32px);
		margin-left: 16px;
		height: 12px;
		background: #585858;
		box-shadow: 0px 10px 18px 0px rgba(219, 165, 3, 0.2) inset;
		border-radius: 0px 0px 15px 15px;
	}

</style>
